import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-detail-deliver',
  templateUrl: './detail-deliver.component.html',
  styleUrls: ['./detail-deliver.component.scss'],
})
export class DetailDeliverComponent implements OnInit {
  @Input() request: any;
  @Output() unSelectrequest= new EventEmitter();
  @Output() statusRequest = new EventEmitter();

  constructor() { }

  ngOnInit() {
    this.organiceRequest(this.request)
  }
  organiceRequest(element){
    let response ={
      _id: element._id,
      client:element.client,
      numberPhone:element.numberPhone,
      clientId: element.clientId,
      date: element.date,
      destiny: element.destiny,
      distance: 1,
      origin: element.origin,
      product: element.product,
      status: element.status,
      userId: element.userId,
      value: parseInt(element.price),
    }
    this.request = response;
  }
  unSelect(id){
    this.unSelectrequest.emit(false)
    this.statusRequest.emit({
      id,
      status:0
    })
  }

  finalice(id){
    this.unSelectrequest.emit(false)
    this.statusRequest.emit({
      id:this.request._id,
      status:3
    })
  }
}
